export default class BamForm {
  constructor(
    extroversion,
    recharge,
    intro,
    drivesNuts,
    qualities,
    misunderstandings,
    communicate,
    feedback
  ) {
    this.extroversion = extroversion;
    this.intro = intro;
    this.recharge = recharge;
    this.drivesNuts = drivesNuts;
    this.qualities = qualities;
    this.misunderstandings = misunderstandings;
    this.communicate = communicate;
    this.feedback = feedback;
  }

  isValid() {
    let hasContent = -1;

    for (const key in this) {
      if (key !== "extroversion") {
        if (this[key] != null && this[key] !== "") {
          hasContent = 1;
        }
        else
        {
          if(hasContent >= 0)
          {
            hasContent = 0
          }
        }
      }
    }

    return hasContent;
  }
}
