<template>
    <div class="main-intro">
        <SkeletonLoader
            v-if="waitingFunc"
            width="100%"
            height="100px"
            class="skeleton-style"
            />

        <textarea
            v-else
            :value="modelValue"
            @input="$emit('update:modelValue', $event.target.value)"
            :placeholder="hint"
            :disabled="disablingFunc">
        </textarea>

    </div>
</template>

<script>
    import { SkeletonLoader } from 'vue3-loading-skeleton'
    import 'vue3-loading-skeleton/dist/style.css'

    export default {
    name: 'IntroductionBlock',
    components: {SkeletonLoader },
    props: {
        hint: {type: String, default: "Type Here"},
        disablingFunc: { type: Boolean, default: false },
        modelValue: {type: String, default: ''},
        waitingFunc: { type: Boolean, default: false }
    },
    emits: ['update:modelValue']
}
</script>

<style scoped>

.skeleton-style {
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.2);
    --skeleton-color: rgba(255, 255, 255, 0.1);      /* shimmer background */
    --skeleton-highlight: rgba(255, 255, 255, 0.2);  /* shimmer wave */
    }


.main-intro textarea {
    border-radius: 10px;
    font-size: calc(13px + var(--font-gain));
    padding: 8px;
    width: 100%;
    height: 80px;
    resize: none;
    opacity: 0.6;
    background-color: white;
    border: 1px solid #ccc;
    color: black;
    }

    .main-intro textarea:disabled {
    font-size: calc(13px + var(--font-gain));
    padding: 8px;
    width: 100%;
    height: 80px;
    resize: none;
    background-color: white;
    border: 1px solid #ccc;
    opacity: 0.6;
    color: black;
    cursor: not-allowed;
    box-shadow: none;
    filter: none;
    }
</style>