<template>

    <div class="slider-container">
              <input
                id="slider"
                type="range"
                min="0"
                max="1"
                step="0.01"
                :value="modelValue"
                @input="$emit('update:modelValue', $event.target.value)"
                class="slider"
                :disabled="disablingFunc"
                style="width: 100%;"
              />

              <div class="inex-label" style="display: flex; flex-direction: row; justify-content: space-between;">
              <p class="in-label">Introvert</p>
              <p class="ex-label">Extrovert</p>
              </div>
    </div>
              
    <!-- <div class="main-intro">
        <textarea
            :value="modelValue"
            @input="$emit('update:modelValue', $event.target.value)"
            :placeholder="hint"
            :disabled="disablingFunc">
        </textarea>
    </div> -->
</template>

<script>
    export default {
    name: 'ExtroversionMeter',
    props: {
        disablingFunc: { type: Boolean, default: false },
        modelValue: {type: String, default: ''},
        waitingFunc: { type: Boolean, default: false }
    },
    emits: ['update:modelValue']
}
</script>

<style scoped>
.slider {
  -webkit-appearance: none;
  width: 500px;
  height: 8px;
  background: transparent;
}

.slider::-webkit-slider-runnable-track {
  background: #A2BCE0;
  border-radius: 4px;
}

.slider::-webkit-slider-thumb{
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #4A90E2;
  cursor: pointer;
  margin-top: -5px;
  margin-bottom: -5px;
  border: none;
}

.slider:disabled::-webkit-slider-thumb {
  background: #4A90E2;
  opacity: 1;
  filter: none;
}

.inex-label {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.in-label,
.ex-label {
  font-weight: bold;
  font-size: calc(20px + var(--font-gain));
}

</style>