<template>
    <div class="base-button" :class="{ 'd-none': hideFunc }">
        <button 
                @click="$emit('func')" 
                :disabled="disablingFunc"
                :class="[layout === 0 ? $attrs.class : '', layout === 1 ? 'button1' : '', layout === 2 ? 'button2' : '', layout === 3 ? 'button3' : '']"
                :style="{backgroundColor: backgroundColor}">
                {{ disablingFunc ? disableText : normalText }}
        </button>
    </div>
</template>

<script>
    export default {
    name: 'BaseButton',
    props: {
        disablingFunc: { type: Boolean, default: false },
        hideFunc: { type: Boolean, default: false },
        backgroundColor: {type: String, default: "#ffffff"},
        normalText: {type: String, default: "Enabled"},
        normalTextSmall: {type: String, default: "E"},
        disableText: {type: String, default: "Disabled"},
        disableTextSmall: {type: String, default: "D"},
        layout: { type: Number, default: 1 }
    },

    data()
    {
        return {
            factor: {
                w: 200/15,      
                h: 30/15,       
                fs: 12/15,      
                br: 15/15       
            }
        }
    },

    methods: {

        // Used to dynamically set the size of the buttons according to the word-length. 
        // Factoring Problem - 'factor' is not good for words with few characters
        // :style="setStyle(normalText.length * { 1: 0.5, 2: 1, 3: 2, 4: 4 }[layout])">
        setStyle(C)
        {
            return {
                width:      `${this.factor.w * C}px`,
                height:     `${this.factor.h * C}px`,
                fontSize:   `${this.factor.fs * C}px`,
                borderRadius:`${this.factor.br * C}px`,
                margin: '5 auto',
                backgroundColor: this.backgroundColor
                };
        }
     }
    };
</script>

<style scoped>

.button1
{
    width: 100px; 
    height: 15px; 
    font-size: 6px; 
    border-radius: 7px;  
    margin: 5 auto;
}

.button2
{
    width: 200px; 
    height: 30px; 
    font-size: 12px; 
    border-radius: 15px;  
    margin: 5 auto;
}

.button3
{
    width: 400px; 
    height: 60px; 
    font-size: 24px; 
    border-radius: 30px;  
    margin: 5 auto;
}

.d-none {
  display: none;
}


</style>