<template>
  <div id="app">
    <div class="video-background">
    <video autoplay muted loop playsinline>
    <source src="bam_background.mp4" type="video/mp4">
    Your browser does not support the video tag.
    </video>
    </div>


    <FontControl
      class="font-control"
      :max-disabled="maxFontDisabled"
      :min-disabled="minFontDisabled"
      @inc="incFontSize"
      @reset="resetFontSize"
      @dec="decFontSize"
    />

      <BaseButton
        class="main-button"
        @func = "viewForm"
        :disabling-func="loading"
        :hide-func="hideView"
        normal-text="View All Submitted Forms"
        disable-text="Loading..."
        :layout="0"
      />

      <BaseButton
        class="main-button"
        @func = "editForm"
        :disabling-func="loading"
        :hide-func="hideEdit"
        normal-text="Submit New Form"
        disable-text="Loading..."
        :layout="0"
      />

    <div style="display: flex; align-items: center; justify-content: center; gap: 1rem;">


      <BaseButton
        class="nav-button"
        id="nav-btn-1"
        @func = "goPrev"
        :background-color="'#ffffff5d'"
        :disabling-func="(loading || !prev) || waiting"
        :hide-func="hideEdit"
        normal-text="<"
        disable-text="<"
        :layout="0"
      />


      <div class="content">
        <!-- Title and Introvert/Extrovert -->
        <div class="title-and-inexmeter">
          <div class="title">
          <p>Bit About Me...</p>
          </div>

          <div style="width: 400px;">

            <ExtroversionMeter
              v-model="sliderValue"
              :disabling-func="isDisabled"
              :waiting-func="waiting"
            />

            <QaBlock
              v-model="recharge"
              title="Recharge by"
              hint="Describe"
              :disabling-func="isDisabled"
              :waiting-func="waiting"
            />
          
          </div>
        </div>

        <!-- Main Introduction -->
        <IntroductionBlock
            v-model="intro"
            hint="Introduce Yourself"
            :disabling-func="isDisabled"
            :waiting-func="waiting"
          />

        <!-- Other Intros -->

        <div class="qa-container">

          <QaBlock
            v-for="(key, index) in keys"
            :key="key"
            :modelValue="$data[key]"
            @update:modelValue="val => $data[key] = val"
            :title="titles[index]"
            hint="Describe"
            :disabling-func="isDisabled"
            :waiting-func="waiting"
          />

        </div>

        <BaseButton
          class="submit-button"
          @func = "submitForm"
          :disabling-func="submitting"
          :hide-func="hideView"
          normal-text="SUBMIT"
          disable-text="submitting..."
          :layout="0"
        />

      </div>
        <BaseButton
        class="nav-button"
        id="nav-btn-2"
        @func = "goNext"
        :background-color="'#ffffff5d'"
        :disabling-func="(loading || !next) || waiting"
        :hide-func="hideEdit"
        normal-text=">"
        disable-text=">"
        :layout="0"
      />
      </div>
  </div>
</template>

<script>
import FontControl from './components/FontControl.vue';
import BaseButton from './components/BaseButton.vue';
import QaBlock from './components/QaBlock.vue';
import IntroductionBlock from './components/IntroductionBlock.vue';
import ExtroversionMeter from './components/ExtroversionMeter.vue';


import { useToast } from 'vue-toastification'
const toast = useToast()

import BamForm from './BamForm'


export default {
  name: 'App',
  components: { FontControl, BaseButton, QaBlock, IntroductionBlock, ExtroversionMeter },

  data() {
    return {
      apiMessage: '',
      loading: false,
      waiting: false,
      submitting: false,
      error: null,

      sliderValue: 0.5,
      intro: '',
      recharge: '',
      drivesNuts: '',
      qualities: '',
      misunderstandings: '',
      communicate: '',
      feedback: '',

      keys: ['drivesNuts', 'qualities', 'misunderstandings', 'communicate', 'feedback'],
      titles: ["Drive me nuts when","Value these qualities in co-workers", "Things People might misunderstand about me", "Best way to communicate with me", "Ways I Prefer to get feedback"],

      hideView: false,
      hideEdit: true,

      formNum: 1,

      next: true,
      prev: false,

      isDisabled: false,

      fontGain: 0,

      maxFontDisabled: false,
      minFontDisabled: false,
    }
  },

  created()
  {
    // Read the form number from the local storage. If not  available create the variable in local storage.
    const formNumStr = localStorage.getItem('formNum');
      if(formNumStr !== null)
      {
        const number = Number(formNumStr);

        if(number > 0)
        {    
          this.formNum = number;
        }
        else
        {
          this.formNum = 1;
          localStorage.setItem('formNum', this.formNum);
        }
      }
      else
      {
        localStorage.setItem('formNum', this.formNum);
      }
  },

  mounted(){
    document.documentElement.style.setProperty('--font-gain', `${this.fontGain}px`);
  },

  watch: {
    fontGain(newValue) {
      document.documentElement.style.setProperty('--font-gain', `${newValue}px`);
    }
  },

  methods: {

    async serverCheck() {
      try {
        const response = await fetch('https://bam-server-u2xd.onrender.com/api/form', {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          }
        });
        return response.ok;
      } catch (error) {
        return false;
      }
    },

    async submitForm() {

      const form = new BamForm(this.sliderValue, this.recharge, this.intro, this.drivesNuts,
                this.qualities, this.misunderstandings, this.communicate, this.feedback);
      

      if(form.isValid() >= 0)
      { 
        this.submitting = true;
        const serverIsAvailable = await this.serverCheck();

        if (!serverIsAvailable) {
          this.submitting = false;
          toast.error("Server not available.");
          return;
        }       

        fetch('https://bam-server-u2xd.onrender.com/api/form', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
            body: JSON.stringify(form),
          })
          .then(async res => {
            const data = await res.json();
            if (!res.ok) {
              throw new Error(data.message || res.statusText);
            }
            return data;
          })
          .then(json => {
            this.apiMessage = json.message
            this.clearFields();
            if(form.isValid() == 1)
            {
              toast.success("Form Submitted")
            }
            else
            {
              toast.warning("Partial Form Submitted")
            }
          })
          .catch(err => {
            toast.error("Error: " + err.message)
          })
          .finally(() => {
            this.submitting = false
          })
      }
      else
      {
        toast.error('Empty Form!')
      }
    },

    async viewForm() {

      console.log("HI view");
      this.loading = true;
      const serverIsAvailable = await this.serverCheck();

      if (!serverIsAvailable) {
        this.loading = false;
        toast.error("Server not available.");
        return;
      }

      this.hideView = true
      this.hideEdit = false
      this.clearFields()
      this.getForm()
      this.isDisabled = true
      this.loading = false;

    },

    editForm() {
      this.waiting = false
      this.hideView = false
      this.hideEdit = true
      this.clearFields()
      this.isDisabled = false
    },

    getForm(){
    this.waiting = true
    this.clearFields()

    console.log("HI");
      // Get the form details for the corresponding form number
    fetch(`https://bam-server-u2xd.onrender.com/api/form/${this.formNum}`,
    {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    })
    .then(async res => {
      console.log("Hello0000000000 1");
      const data = await res.json()
      if (!res.ok) {
        console.log("Hello");
        throw new Error(data.message || res.statusText)
      }
      return data
    })
    .then(json => {
       console.log("Hello0000000000");
      if(this.hideView) // Ensure that the user is in View Mode
      {
        const form = json.data
        this.sliderValue      = parseFloat(form.extroversion) || 0.5
        this.intro             = form.intro ?? null;
        this.recharge          = form.recharge ?? null;
        this.drivesNuts        = form.drivesNuts ?? null;
        this.qualities         = form.qualities ?? null;
        this.misunderstandings = form.misunderstandings ?? null;
        this.communicate       = form.communicate ?? null;
        this.feedback          = form.feedback ?? null;

        this.next             = json.nextFlag // True if there is a form available for the next index
        this.prev             = json.prevFlag // True if there is a form available for the previous index
      }
    })

   .finally(() => {
            this.waiting = false
          })
    },

    goPrev(){
      this.formNum--;
      localStorage.setItem('formNum', this.formNum);
      this.getForm();
    },

    goNext(){
      this.formNum++;
      localStorage.setItem('formNum', this.formNum);
      this.getForm();
    },

    incFontSize(){
      if(this.fontGain < 5){
        this.fontGain++;
        this.minFontDisabled = false;
      }
      else{
      this.maxFontDisabled = true
      }
    },

    resetFontSize(){
      this.fontGain = 0;
      this.maxFontDisabled = false;
      this.minFontDisabled = false;
    },

    decFontSize(){
      if(this.fontGain > -5){
        this.fontGain--;
        this.maxFontDisabled = false;
      }
      else{
      this.minFontDisabled = true
      }
    },

    clearFields()
    {
      this.sliderValue      = 0.5
      this.intro             = null;
      this.recharge          = null;
      this.drivesNuts        = null;
      this.qualities         = null;
      this.misunderstandings = null;
      this.communicate       = null;
      this.feedback          = null;
    }
  }
}
</script>

