<template>
    <div class="qa">
        <p>{{title}}</p>


        <SkeletonLoader
            v-if="waitingFunc"
            width="300px"
            height="70px"
            class="skeleton-style"
            />

        <textarea
            v-else
            :value="modelValue"
             @input="$emit('update:modelValue', $event.target.value)"
            :placeholder="hint"
            :disabled="disablingFunc">
        </textarea>
    </div>
</template>


<script>
    import { SkeletonLoader } from 'vue3-loading-skeleton'
    import 'vue3-loading-skeleton/dist/style.css'

    export default {
    name: 'QaBlock',
    components: {SkeletonLoader },
    props: {
        title: {type: String, default: "Untitled"},
        hint: {type: String, default: "Type Here"},
        disablingFunc: { type: Boolean, default: false },
        modelValue: {type: String, default: ''},
        waitingFunc: { type: Boolean, default: false }
    },
    emits: ['update:modelValue']
}
</script>

<style scoped>
    .qa{
    text-align: left;
    font-size: calc(25px + var(--font-gain));
    font-weight: 700;
    font-family: Comic Neue;
    }

    .qa textarea{
    border-radius: 10px;
    opacity: 0.6;
    font-size: calc(13px + var(--font-gain));
    padding: 8px; 
    width: 300px; 
    height: 50px; 
    resize: none;
    }

    @media (max-width: 810px) {
    .qa textarea{
        border-radius: 10px;
        opacity: 0.6;
        font-size: calc(13px + var(--font-gain));
        padding: 8px; 
        width: 100%; 
        height: 50px; 
        resize: none;
        }
    }


    .qa textarea:disabled {
    opacity: 0.6;
    background-color: white; 
    border: 1px solid #ccc;
    color: black; 
    cursor: not-allowed; 
    box-shadow: none; 
    filter: none;
    }

    .skeleton-style {
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.2);;
    --skeleton-color: rgba(255, 255, 255, 0.1);      /* shimmer background */
    --skeleton-highlight: rgba(255, 255, 255, 0.2);  /* shimmer wave */
    }

</style>